import { Component, OnInit } from '@angular/core';
import { MyserviceService } from '../myservice.service';
import { Router ,ActivatedRoute } from '@angular/router';
import { NgForm,FormGroup,FormControl, Validators } from '@angular/forms';
@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {
  myIForm:FormGroup;
  companies;
  successmsg;
  status;
  constructor(private _myservice:MyserviceService,
    private _router: Router,private _activatedRoute:ActivatedRoute) { 
    this.myIForm = new FormGroup({
      studentname:new FormControl(null,Validators.required),
      password:new FormControl(null,Validators.required),
      studentid:new FormControl(null,Validators.required),
      studentCGPA:new FormControl(null,Validators.required),
      resume: new FormControl(" ",Validators.required),
      FE: new FormControl(false,Validators.required),
      SE: new FormControl(false,Validators.required),
      TE: new FormControl(false,Validators.required),
      BE: new FormControl(false,Validators.required),
      COMP: new FormControl(false,Validators.required),
      ENTC: new FormControl(false,Validators.required),
      IT: new FormControl(false,Validators.required),
    });

    this._myservice.getloginstatus()
    .subscribe(
      data => {
      this.status=data;
      if(this.status.status==false)
      this._router.navigate(['../adminlogin']);
    },
      error => {this._router.navigate(['../adminlogin'])});
   } 
  
  ngOnInit() {
    
  }
  isValid(controlName)
  {
    return this.myIForm.get(controlName).invalid&&this.myIForm.get(controlName).touched;
  }

 viewexisting()
 {
  document.getElementById("two").style.display="block";
  document.getElementById("three").style.display="none";
  var obj={};
  this._myservice.viewcompany(obj)
  .subscribe(
    data => this.companies=data,
    error => {this._router.navigate(['../admin'])});
 }
 studentprofile()
 {
  document.getElementById("two").style.display="none";
  document.getElementById("three").style.display="block";
 }
 newstudent()
 {
  this._myservice.addstudent(this.myIForm.value).subscribe(
    data=> this.successmsg='Successfully  Created New Student Profile',
    error=> this.successmsg='Error Occured Try Later'
  );
 }
 deletecompany(cname)
 {
   var obj={cname:cname};
  this._myservice.deletecompany(obj).subscribe();
  this.viewexisting();
 }
 logout()
 {
  this._myservice.adminlogout()
  .subscribe(
    data => {
    this.status=data;
    if(this.status.status==false)
    this._router.navigate(['../adminlogin']);
  },
    error => {this._router.navigate(['../adminlogin'])});
 }
} 
