import { Component, OnInit } from '@angular/core';
import { NgForm,FormGroup,FormControl, Validators } from '@angular/forms';
import {MyserviceService} from '../../myservice.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-newcompany',
  templateUrl: './newcompany.component.html',
  styleUrls: ['./newcompany.component.css']
})
export class NewcompanyComponent implements OnInit {

  myForm:FormGroup;
  successMessage='';
  constructor(private _myservice:MyserviceService,private _router:Router,private _activatedRoute:ActivatedRoute) {
    this.myForm = new FormGroup({
      companyemail: new FormControl(null,Validators.email),
      companyname: new FormControl(null,Validators.required),
      companyurl: new FormControl(null,Validators.required),
      companycontact: new FormControl(null,Validators.required),
      password: new FormControl(null,Validators.required)
    });
   }

  ngOnInit() { 
  }

  isValid(controlName)
  {
    return this.myForm.get(controlName).invalid&&this.myForm.get(controlName).touched;
  }

  companyregister(){ 
    this._myservice.submitRegister(this.myForm.value).subscribe(
      data=> this.successMessage='Registration Success',
      error=> this.successMessage='Some Error'
    );
  }
  movetologin(){
    this._router.navigate(['../companylogin'],{relativeTo:this._activatedRoute});
  }

}
