import { Component, OnInit } from '@angular/core';
import { NgForm,FormGroup,FormControl, Validators } from '@angular/forms';
import {MyserviceService} from '../myservice.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-adminlogin',
  templateUrl: './adminlogin.component.html',
  styleUrls: ['./adminlogin.component.css']
})
export class AdminloginComponent implements OnInit {
  loginForm:FormGroup;
  constructor(private _myservice:MyserviceService,private _router:Router,private _activatedRoute:ActivatedRoute) {
    this.loginForm=new FormGroup({
      Adminemail: new FormControl(null,Validators.email),
      password:new FormControl(null,Validators.required)
    });
    
   }

  ngOnInit() {
  }

  isValid(controlName)
  {
    return this.loginForm.get(controlName).invalid&&this.loginForm.get(controlName).touched;
  }

  Clogin(){
    console.log(this.loginForm.value);
    this._myservice.Alogin(this.loginForm.value).subscribe(
      data=>{ 
      this._router.navigate(['/admin']);
       },
  error =>{ console.log("INVALID CRED");});
  }


} 
