import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl, Validators } from '@angular/forms';
import {MyserviceService} from '../../myservice.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-studentlogin',
  templateUrl: './studentlogin.component.html',
  styleUrls: ['./studentlogin.component.css']
})
export class StudentloginComponent implements OnInit {
  loginForm:FormGroup;
  constructor(private _myservice:MyserviceService,private _router:Router,private _activatedRoute:ActivatedRoute) {
    this.loginForm=new FormGroup({
      studentid: new FormControl(null,Validators.required),
      password:new FormControl(null,Validators.required)
    });
   }

  ngOnInit() {
  }
  isValid(controlName)
  {
    return this.loginForm.get(controlName).invalid&&this.loginForm.get(controlName).touched;
  }
  slogin(){
    this._myservice.slogin(this.loginForm.value).subscribe(
      data=>{ console.log(data);
      localStorage.setItem('token',data.toString());
      this._router.navigate(['/student']);
       },
  error =>{ console.log("INVALID STUDENT CRED");});

  }

}
