import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { HeaderComponent } from './header/header.component';
import { StudentloginComponent } from './student/studentlogin/studentlogin.component';
import { CompanyloginComponent } from './company/companylogin/companylogin.component';
import { StudenthomeComponent } from './student/studenthome/studenthome.component';
import { CompanyhomeComponent } from './company/companyhome/companyhome.component';
import {RouterModule,Routes} from '@angular/router';
import {FormsModule,ReactiveFormsModule} from "@angular/forms";
import { NewcompanyComponent } from './company/newcompany/newcompany.component';
import { AdminComponent } from './admin/admin.component';
import { MyserviceService } from './myservice.service';
import { HttpClientModule } from '@angular/common/http';
import { AdminloginComponent } from './adminlogin/adminlogin.component';

const routes: Routes=
[
  {path:'home',component:HomeComponent},
  {path:'student',component:StudenthomeComponent},
  {path:'company',component:CompanyhomeComponent},
  {path:'studentlogin',component:StudentloginComponent},
  {path:'companylogin',component:CompanyloginComponent},
  {path:'newCompany',component:NewcompanyComponent},
  {path:'admin',component:AdminComponent},
  {path:'adminlogin',component:AdminloginComponent},
  {path:'',redirectTo:'home',pathMatch:'full'}
];

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    HeaderComponent,
    StudentloginComponent,
    CompanyloginComponent,
    StudenthomeComponent,
    CompanyhomeComponent,
    NewcompanyComponent,
    AdminComponent,
    AdminloginComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule
  ],
  providers: [MyserviceService],
  bootstrap: [AppComponent]
})
export class AppModule { }
